export const bp1 =
`
In the reverse process, we simply pass the noisy version of the image to our denoiser model, and subtract* 
the predicted noise from the original image.

The predicted noise by the model is supposed to be ~ total noise added to the original image, however, when subtracting noise, 
we remove only a part of it in each time step. This allows for better results when it comes to actual image generation.
`.trim();


