
export const introtext1 = 
`
Stable diffusion is a Generative technique that has taken the world by storm, rightly so. The images it can produce 
are good, _way too good_ for something that was completely fabricated. But how does it actually work? 
In this deepdive, we'll see how it works, and more importantly _why it works_. We'll take a look at both the theory, 
and making of a very simple and rudimentary diffusion based generative model.

Over the course of this post, you'll realise that stable diffusion is more about the things _around the model_, rather 
than some new architecture itself.

_This post is meant to be an explainer for the code over at [this model I created](https://github.com/StealthyPanda/stablediffusion)._

`.trim();