export const p1 =
`
Let's talk about the denoiser model. This model is usually a [UNet](https://arxiv.org/abs/1505.04597), which are basically models that 
apply convolutions to reduce height and width, while simultaneously increasing depth. UNets are quite 
popular for their use in Image Segmentation. For our purposes, this denoiser will be based on a UNet, taking in 
the noisy image and the time step of the image as the input, and returning the amount of noise present in 
the image as the output.
`.trim();

