export const p3 =
`
UNet can be divided into blocks, where each block either down samples or upsamples the input image. 
The architecture of these blocks is relatively simple. All they do is apply convolutions with strides, and ReLU activations.

Depending on the direction (up or down sampling), the strides and padding are accordingly set.

There are also connections between UBlocks that have the same output size (this is not the same as residual connections) 
that allows for better learning. These connections can be in the form of concatenation (as in the diagram), or simply adding the 
two tensors, which is what I did in the model. All the activation functions used are simple ReLUs.
`.trim();

export const p4 =
`
The time input, which is an integer, will be converted to [positional encodings](https://machinelearningmastery.com/a-gentle-introduction-to-positional-encoding-in-transformer-models-part-1/), which are just vectors representing information about the unique time step 
we are currently in. This time input is passed as input to each UBlock, and is required as the model has to use the same weights and biases 
for different time steps, and so also the differing amounts of noise that has been added to the image.

These time vectors are simply added to the input image at each UBlock step, and then the image is treated just the same.
`.trim();

